body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

body {
 background: #f6fcf8;
 color: #3a1d15;
 font-family: 'Courier New', Courier, monospace;
}

.container {
  margin: 20px;
  justify-content: center;
  margin: 0 auto;
}

.footer {
  background: red;
}
.nav {
    background: linear-gradient(to right, #b98c38, #f6fcf8);
}

.nav-text{
    margin: 0px 5%;
    padding: 10px 0px;
    font-size: 1.5em;
    color: #3a1d15;
    font-family: 'Courier New', Courier, monospace;
}
.card {
    border: 10px groove;
    margin: 5px;
    float: left;
}

img {
    height: 150px;
    width: 150px;
}
